




















import Vue from "vue";

export default Vue.extend({
  props: {
    pattern: {
      type: RegExp,
    },
    placeHolder: {
      type: String,
      default: "",
    },
    describedby: {
      type: String,
      default: "",
    },
    title: {
      type: String,
      default: "",
    },
    type: {
      type: String,
      default: "text",
    },
  },
  computed: {
    idState(): boolean | null {
      if (this.value.length === 0) return null;
      if (this.value.match(new RegExp(this.pattern))) {
        return true;
      } else {
        return false;
      }
    },
  },
  data() {
    return {
      value: "",
    };
  },
  methods: {
    onKeyup() {
      this.$emit("@keyup", this.value);
    },
  },
});
